<template>
    <div class="myRegiment">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">我的哼哈团</span>
            <span class="back"></span>
            <span class="about">关于</span>
        </div>
        <div class="tips">
          <span>您还没有哼哈团</span>
          <span>您可以创建一个属于自己的哼哈团或者您也可以选择查看哼哈团列表加入自己感兴趣的哼哈团</span>
        </div>
        <div class="actions">
          <button class="create" @click="create">创建团</button>
          <button class="jion" @click="jion">加入团</button>
        </div>
    </div>
</template>

<script>
export default {
  name: 'myRegiment',
  data () {
    return {
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    create () {
      this.$router.push('/home/mine/createRegiment')
    },
    jion () {
      this.$router.push('/home/mine/regimentList')
    }
  }
}
</script>

<style scoped>
 * {
    margin: 0;
    padding: 0;
    color: #333333;
    font-size: 0.48rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 99;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .about {
    position: absolute;
    right: 0;
    font-size: 0.42667rem;
  }

  .tips {
    width: calc(100% - 1.44rem);
    margin: 0 auto;
    margin-top: 5.7rem;
  }

  .tips span {
    display: inline-block;
  }

  .tips span:first-child {
    font-weight: 500;
  }

  .tips span:last-child {
    font-size: 0.37333rem;
    text-align: start;
    margin-top: 0.4rem;
  }

  .actions {
    width: calc(100% - 1.6rem);
    margin: 0 auto;
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .create,
  .jion {
    width: 3.73333rem;
    height: 1.33333rem;
    outline: none;
    border-radius: 0.15rem;
  }

  .create {
    border: 1px solid #999999;
    background: #fff;
    color: #666666;
  }

  .jion {
    background: #ffd200;
    color: #ffffff;
    border: none;
  }
</style>



